// Example: How to use language support in your dashboard
// File: app/organizer/dashboard/page.tsx

'use client';

import React from 'react';
import { useI18n } from '@/hooks/useI18n';
import { useThemeClasses } from '@/hooks/useTheme';
import {
    DollarSign,
    Users,
    Calendar,
    TrendingUp,
    Clock,
    MapPin
} from 'lucide-react';

const OrganizerDashboard: React.FC = () => {
    const { t, formatCurrency, formatDate, formatTime, currentLangData } = useI18n();
    const themeClasses = useThemeClasses();

    // Sample data
    const stats = [
        {
            title: t('totalRevenue'), // Will show "Total Revenue" in English, "Ingresos Totales" in Spanish
            value: 12450.75,
            icon: DollarSign,
            change: '+12%',
            format: 'currency'
        },
        {
            title: t('totalTicketsSold'), // Will be translated
            value: 1250,
            icon: Users,
            change: '+8%',
            format: 'number'
        },
        {
            title: t('activeEvents'), // Will be translated
            value: 15,
            icon: Calendar,
            change: '+3%',
            format: 'number'
        },
        {
            title: t('conversionRate'), // Will be translated
            value: 0.045,
            icon: TrendingUp,
            change: '+0.5%',
            format: 'percentage'
        }
    ];

    const upcomingEvents = [
        {
            title: 'Tech Conference 2024',
            date: new Date('2024-07-15T14:00:00'),
            location: 'Convention Center',
            ticketPrice: 99.99
        },
        {
            title: 'Music Festival',
            date: new Date('2024-07-20T18:30:00'),
            location: 'Central Park',
            ticketPrice: 75.50
        }
    ];

    const formatValue = (value: number, format: string) => {
        switch (format) {
            case 'currency':
                return formatCurrency(value); // Will use user's selected currency
            case 'percentage':
                return `${(value * 100).toFixed(1)}%`;
            default:
                return value.toLocaleString(currentLangData.region);
        }
    };

    return (
        <div className={`p-6 ${themeClasses.themeBg} min-h-screen`}>
            <div className="max-w-7xl mx-auto">
                {/* Header */}
                <div className="mb-8">
                    <h1 className={`${themeClasses.text3Xl} font-bold ${themeClasses.themeFg} mb-2`}>
                        {t('dashboard')} {/* Will be translated */}
                    </h1>
                    <p className={`${themeClasses.themeMutedFg}`}>
                        {t('welcome')}, {t('organizer')}! {/* Will be translated */}
                    </p>
                </div>

                {/* Stats Grid */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
                    {stats.map((stat, index) => {
                        const Icon = stat.icon;
                        return (
                            <div
                                key={index}
                                className={`${themeClasses.themeCard} p-6 rounded-lg border ${themeClasses.themeBorder} shadow-sm`}
                            >
                                <div className="flex items-center justify-between mb-4">
                                    <Icon className={`w-5 h-5 ${themeClasses.themeMutedFg}`} />
                                    <span className="text-sm text-green-600 font-medium">{stat.change}</span>
                                </div>
                                <div>
                                    <p className={`${themeClasses.textSm} ${themeClasses.themeMutedFg} mb-1`}>
                                        {stat.title}
                                    </p>
                                    <p className={`${themeClasses.text2Xl} font-bold ${themeClasses.themeFg}`}>
                                        {formatValue(stat.value, stat.format)}
                                    </p>
                                </div>
                            </div>
                        );
                    })}
                </div>

                {/* Upcoming Events */}
                <div className={`${themeClasses.themeCard} rounded-lg border ${themeClasses.themeBorder} shadow-sm`}>
                    <div className="p-6">
                        <h2 className={`${themeClasses.textXl} font-semibold ${themeClasses.themeFg} mb-6`}>
                            {t('upcomingEvents')} {/* Will be translated */}
                        </h2>

                        <div className="space-y-4">
                            {upcomingEvents.map((event, index) => (
                                <div
                                    key={index}
                                    className={`p-4 rounded-lg border ${themeClasses.themeBorder} ${themeClasses.hover} transition-colors`}
                                >
                                    <div className="flex items-center justify-between">
                                        <div>
                                            <h3 className={`font-medium ${themeClasses.themeFg} mb-2`}>
                                                {event.title}
                                            </h3>
                                            <div className="flex items-center space-x-4 text-sm">
                                                <div className="flex items-center space-x-1">
                                                    <Clock className={`w-4 h-4 ${themeClasses.themeMutedFg}`} />
                                                    <span className={themeClasses.themeMutedFg}>
                                                        {formatDate(event.date)} {formatTime(event.date)}
                                                        {/* Will use user's selected date/time format */}
                                                    </span>
                                                </div>
                                                <div className="flex items-center space-x-1">
                                                    <MapPin className={`w-4 h-4 ${themeClasses.themeMutedFg}`} />
                                                    <span className={themeClasses.themeMutedFg}>
                                                        {event.location}
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="text-right">
                                            <p className={`${themeClasses.textSm} ${themeClasses.themeMutedFg} mb-1`}>
                                                {t('ticketPrice')} {/* Will be translated */}
                                            </p>
                                            <p className={`font-semibold ${themeClasses.themeFg}`}>
                                                {formatCurrency(event.ticketPrice)}
                                                {/* Will use user's selected currency */}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>

                {/* Language Info Panel (for demonstration) */}
                <div className={`mt-6 ${themeClasses.themeCard} rounded-lg border ${themeClasses.themeBorder} shadow-sm p-4`}>
                    <h3 className={`${themeClasses.textLg} font-semibold ${themeClasses.themeFg} mb-3`}>
                        Current Language Settings
                    </h3>
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                        <div>
                            <span className={`${themeClasses.themeMutedFg}`}>Language:</span>
                            <span className={`ml-2 ${themeClasses.themeFg}`}>
                                {currentLangData.flag} {currentLangData.nativeName}
                            </span>
                        </div>
                        <div>
                            <span className={`${themeClasses.themeMutedFg}`}>Currency:</span>
                            <span className={`ml-2 ${themeClasses.themeFg}`}>
                                {currentLangData.currency}
                            </span>
                        </div>
                        <div>
                            <span className={`${themeClasses.themeMutedFg}`}>Time Format:</span>
                            <span className={`ml-2 ${themeClasses.themeFg}`}>
                                {currentLangData.timeFormat === '12h' ? '12-hour' : '24-hour'}
                            </span>
                        </div>
                        <div>
                            <span className={`${themeClasses.themeMutedFg}`}>Date Format:</span>
                            <span className={`ml-2 ${themeClasses.themeFg}`}>
                                {currentLangData.dateFormat}
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default OrganizerDashboard;